import React from "react";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { useEffect, useState } from "react";
import { RiUser3Fill } from "react-icons/ri";
import { FaIdCard } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import Nav from "../../components/Navbar/Nav";
const Users = () => {
  const [allfeedbacks, Setallfeedbacks] = useState([]);

  const getAllfeedback = async () => {
    try {
      const { data } = await axios.get("/api/user/allfeedback");
      if (data?.success) {
        Setallfeedbacks(data.feedbacks);
        toast.success(data.message);
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };

  useEffect(() => {
    getAllfeedback();
  }, []);

  return (
    <div>
      <Nav />
      <div className="admin_main">
        <hr />
        <div className="d-flex row container-fluid p-3 ">
          <div className="col-md-3">
            <AdminMenu />
          </div>
          <div className="col-md-9 ps-5  ">
            <div className=" p-3 w-75 ps-5 ">
              <h1 className="text-uppercase mb-3 mt-0 fw-bold text-center   font-monospace">
                feedbacks{" "}
              </h1>
              <div>
                <div className="d-flex row gap-3">
                  {allfeedbacks
                    .map((data) => (
                      <>
                        <div className="card" style={{ width: "18rem" }}>
                          <div className="card-body" key={data._id}>
                            <h4 className="card-text text-capitalize ">
                              <RiUser3Fill className="me-1 h-2" />{" "}
                              {data.username || data.fullname}
                            </h4>
                            <hr />

                            <p>
                              {" "}
                              <MdEmail className="me-1" />
                              {data.email}
                            </p>
                            <p className="bg-warning p-1 ps-2 rounded-3 fw-bold">
                              {" "}
                              {data.message}
                            </p>
                            <hr />

                            <p>
                              {" "}
                              <FaIdCard className="me-1" />
                              {data._id}
                            </p>
                          </div>
                        </div>
                      </>
                    ))
                    .reverse()}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Users;
